<template>
  <div class="home">
    <div class="box">
      <div class="wrapper">
        <ul>
          <li v-for="(item,itemIndex) in gameList" :key="itemIndex">
            <span v-for="(child,childIndex) in item" :key="childIndex"></span>
          </li>
        </ul>
        <ol>
          <li v-for="(item,itemIndex) in gameList" :key="itemIndex">
            <em
              v-for="(child,childIndex) in item"
              :key="childIndex"
              :style="{left:`${child.left}px`,top:`${child.top}px`}"
              :class="`_${child.num}`"
            >{{child.num}}</em>
          </li>
        </ol>
      </div>
    </div>
  </div>
</template>

<script>
import { log } from 'util'
export default {
  name: 'home',
  data() {
    return {
      gameList: [[], [], [], []],
      addArr: [[], [], [], []]
    }
  },
  methods: {
    initBox() {
      this.setPosition()
    },
    randomPiece() {
      const x = Math.floor(Math.random() * 4)
      const y = Math.floor(Math.random() * 4)
      !this.gameList[x][y] || this.gameList[x][y].num === 0
        ? (this.gameList[x][y].num = 2)
        : this.randomPiece()
    },
    setPosition() {
      let arr = Array.from(new Array(16))
      this.gameList = [[], [], [], []]
      arr.forEach((item, index) => {
        let ind = parseInt(Math.floor(index / 4))
        this.gameList[ind].push({
          left: (index % 4) * 110,
          top: Math.floor(index / 4) * 110,
          num: 0
        })
      })
      this.randomPiece()
      this.randomPiece()
    },
    keydown(ev) {
      switch (ev.keyCode) {
        case 39:
          this.right(this.gameList)
          break
      }
    },
    right(arr) {
      console.log(this.canMoveRight(arr));
      if (!this.canMoveRight(arr)) return false;
      this.resetAddArr()
      for (var i = 0; i < 4; i++) {
        for (var j = 2; j >= 0; j--) {
          //最后一列的数字不可能向右移动
          if (arr[i][j].num != 0) {
            //(i,j)右侧的元素
            for (var k = 3; k > j; k--) {
              //落脚位置的是否为空 && 中间没有障碍物
              if (arr[i][k].num === 0 && this.noBlockHorizontal(i, j, k, arr)) {
                // move
                // showMoveAnimation(i, j, i, k);
                arr[i][k].num = arr[i][j].num;
                arr[i][j].num = 0;
                continue;
              }
              //落脚位置的数字和本来的数字相等 && 中间没有障碍物
              else if (
                arr[i][k].num == arr[i][j].num &&
                this.noBlockHorizontal(i, j, k, arr)
              ) {
                //move
                // showMoveAnimation(i, j, i, k);
                //add
                if (this.addArr[i][k] !== 0) {
                  arr[i][k - 1].num = arr[i][j].num;
                  arr[i][j].num = 0;
                } else {
                  arr[i][k].num += arr[i][j].num;
                  arr[i][j].num = 0;
                  this.addArr[i][k] = 1;
                  // score += arr[i][k].num;
                }
                continue;
              }
            }
          }
        }
      }
    },
    resetAddArr() {
      for (var i = 0; i < 4; i++) {
        for (var j = 0; j < 4; j++) {
          this.addArr[i][j] = 0;
        }
      }
    },
    canMoveRight(arr) {
      for (var i = 0; i < 4; i++)
        for (var j = 0; j < 4; j++)
          if (arr[i][j].num !== 0 && j !== 3)
            if (arr[i][j + 1].num === 0 || arr[i][j + 1].num === arr[i][j].num) return true;
      return false;
    },
    //
    noBlockHorizontal(row, col1, col2, arr) {
      for (var i = col1 + 1; i < col2; i++) if (arr[row][i].num != 0) return false;
      return true;
    },
    run() {
      this.right(this.gameList)
    }
  },
  mounted() {
    this.initBox()
    this.$warning('建设中...')
    // window.addEventListener('keydown', this.keydown, true)
  },
  destroyed() {
    // window.removeEventListener('keydown', this.keydown, true)
  }
}
</script>

